import axios from "axios";

export const baseURL = `${process.env.REACT_APP_PROXY_URL}https://results.api.beta.siuscloud.com/api/v1/`;

const API = axios.create({
  baseURL
});

API.defaults.headers.common.authorization = process.env.REACT_APP_API_KEY;

API.interceptors.response.use(
  (response) => response,
  (error) => {
    if (error.response.status === 401) {
      // eslint-disable-next-line
      console.log({ error });
    }
    return Promise.reject(error.response.data);
  }
);
const prefix = "pub";
const apiRoutes = {
  COMPETITIONS: `${prefix}/competitions`
};

export default API;
export { apiRoutes };
