import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { Box } from "rebass";
import { createBrowserHistory } from "history";
import routes from "./_app/routing/routes";
import HeaderBar from "./components/HeaderBar/HeaderBar";
import SideMenu from "./components/SideMenu/SideMenu";
import CompetitionsPage from "./components/Competitions/CompetitionsPage";
import Home from "./components/Home/Home";

const history = createBrowserHistory();

class App extends React.Component {
  render() {
    return (
      <Router history={history}>
        <Box id="page-container-outer">
          <SideMenu history={history} />
          <Box id="page-container-inner" style={{ minHeight: "100vh" }}>
            <HeaderBar />
            <Switch>
              <Route exact path={routes.home} component={Home} />
              <Route
                exact
                path={routes.competitions}
                component={CompetitionsPage}
              />
              <Route path={routes.partners}>
                <div style={{ paddingTop: "76px" }}>Partners</div>
              </Route>
              <Route path={routes.contacts}>
                <div style={{ paddingTop: "76px" }}>Contacts</div>
              </Route>
            </Switch>
          </Box>
        </Box>
      </Router>
    );
  }
}

export default App;
