import { observable, action } from "mobx";
import API, { apiRoutes } from "../_app/api";

export class competitionsStore {
  @observable isLoading = false;

  @observable competitions = [];

  @action getCompetitions = async () => {
    this.isLoading = true;
    this.competitions = [];
    try {
      const data = await API.get(apiRoutes.COMPETITIONS);
      this.competitons = this.competitions.push(...data.data);
    } catch (e) {
      // eslint-disable-next-line
      console.log(e);
    } finally {
      this.isLoading = false;
    }
  };

  @action clearStore = async () => {
    this.isLoading = false;
    this.competitions = [];
  };
}

export default new competitionsStore();
